<template>
    <div>
       <!-- <div class="ImgDecode">
        <div class="dropzone">
          <input 
            type="file"
            @change="decodeImg"
          /> 
        </div>
      </div>  -->

      <div class="ImgDecode">
        <div id="dropzone">
          <input type="file" @change="decodeImg" title="">
        </div>
        <h2>Вставьте сюда фотографию</h2>
      </div>
      


     



      
      <!-- <table class="w3-table w3-striped">
         <tr v-for="(b,index) in bars_array" :key="index">
              <td>
                 {{ b.code }}
              </td>
         </tr>
         
      </table> -->
      
      <!-- <table border="1">
        <tr v-for="(g, index) in List" :key="index">
          <td>{{ g.imei }}</td>
        </tr>
      </table> -->
    </div>
    
    
  </template>
  
  <script>
  import { BarcodeReader } from 'dynamsoft-javascript-barcode'
  import {store} from "../store"
  export default {  
    name: 'ImgDecode',
    data() {
      return {
    
        pReader: null,
        
      }
    },
    computed:{
      List(){
        return store.state.imeis
      }
    },
    methods: {



      push_barcode(code){
        store.dispatch('pushNewBar',{id:code,exst:''})

          // this.bars_array.push({ code:code, status:''  })
      },
      async decodeImg(e) {
        try {
          const reader = await (this.pReader = this.pReader || BarcodeReader.createInstance());
          let results = await reader.decode(e.target.files[0]);
          for (let result of results) {
                this.push_barcode(result.barcodeText)
                // alert(result.barcodeText);
          }
          if (!results.length) { this.push_barcode('IMEI не найдено'); }
        } catch (ex) {
          let errMsg;
          if (ex.message?.includes("Нет подключения к сети")) {
            errMsg = "Не удалось подключить соединение с сервером. Пожалуйста, проверьте подключение к сети.";
          } else {
            errMsg = ex.message||ex;
          }
          console.error(errMsg);
          alert(errMsg);
        }
        e.target.value = '';
      },

     

    },
    async beforeDestroy() {
      if (this.pReader) {
        (await this.pReader).destroyContext();
        console.log('ImgDecode Component Unmount');
      }
    }
  
  
   
  
  }
  </script>
  
  <style scoped>
  .ImgDecode {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    
  }
#dropzone{
  width: 597px;
    height: 336px;
    border: 2px dashed gray;
    display: flex;
    align-items: center;
    justify-content: center;
}

input[type='file'] {
  width: 400px;
  height: 336px;
  opacity:0;    
  position: absolute;
}
h2{
  display: flex;
  position: absolute;
}
  </style>